import * as THREE from 'three'
import {
	RGBELoader
} from 'three/examples/jsm/loaders/RGBELoader'
import {
	OrbitControls
} from 'three/examples/jsm/controls/OrbitControls'

export default class MyThree {
	constructor(domName) {
		this.domName = domName,
			this.scene = this.initScene()
		this.cube = this.initCube()
		this.scene.add(this.cube)
		this.camera = this.initCamera()
		this.render = this.initRender()
		this.controller = this.initController()
		this.setSceneWithMap()
		this.AnimaionAutoFresh()

		window.addEventListener("dblclick", () => {
			if (document.fullscreenElement) {
				document.exitFullscreen()
			} else {
				this.render.domElement.requestFullscreen()
			}
		})
	}

	initScene() {
		const scene = new THREE.Scene()
		return scene
	}
	initCube() {
		const video = document.getElementsByClassName( 'video' )[0];
		const videotexture = new THREE.VideoTexture( video );
		videotexture.repeat.set(1,1)


		const textture = new THREE.TextureLoader().load( "./wenli2.jpg" );
		// const geometry = new THREE.BoxGeometry(1, 1, 1)
		const material = new THREE.MeshMatcapMaterial({
			color: 0xFFFFff,
			map:videotexture
		})

		const geometry = new THREE.SphereGeometry( 0.5, 32, 16 );

		const cube = new THREE.Mesh(geometry, material)
		return cube
	}
	initCamera() {
		const camera = new THREE.PerspectiveCamera(45,
			window.innerWidth / window.innerHeight,
			0.2,
			200)
		camera.position.set(0, 0.6, 2.7);
		return camera
	}

	initRender() {
		var width = window.innerWidth;
		var height = window.innerHeight;
		const renderer = new THREE.WebGLRenderer({
			antialias: true
		});
		renderer.setPixelRatio(window.devicePixelRatio)
		renderer.setSize(width, height)
		var dom = document.querySelector(this.domName)
		dom.appendChild((renderer.domElement))
		return renderer
	}

	initController() {
		var controller = new OrbitControls(this.camera, this.render.domElement)
		controller.enableDamping = true
		return controller
	}

	AnimaionAutoFresh() {
		var dom = document.getElementsByClassName("container")[0]
		var height = dom.offsetHeight
		var width = dom.offsetWidth

		this.controller.update()

		var x = this.camera.position.x
		var y = this.camera.position.y
		var z = this.camera.position.z

		console.log(x)

		// this.camera.position.set(x+ Math.PI,y  ,z);

		this.render.setSize(width, height)
		this.render.render(this.scene, this.camera)
		this.render.setAnimationLoop(this.AnimaionAutoFresh.bind(this))
	}

	setSceneWithMap() {
		new RGBELoader().load('./006.hdr', (textture) => {
			textture.mapping = THREE.EquirectangularReflectionMapping
			this.scene.background = textture;
			this.scene.enviroment = textture;
		})
	}
}
